.Card{
  height: 20vh;
  overflow: hidden;
  width:24vw;
  background: white;
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  box-shadow: 1px 1px 3px #777;
  user-select: none;

  .card-info{
    color:#444;
    overflow: hidden;
    position:relative;
    top:0%;
    transition: top .3s;
  }
  .card-title{
    height: auto;
    font-size: 2vw;
    padding-left: 7px;
    text-shadow: 0 0 2px #fff;
    position: relative;
    z-index:5;
  }
  .card-desc{
    font-weight: 300;
    font-size: 1.5vw;
    background: rgb(255,255,255);
    padding: 7px;
    height: auto;
    position: relative;
    overflow: hidden;
  }
  .card-desc.logo{
    text-align: center;
  }
  .licon{
    width:15%;
    height: auto;
    margin-top:1vh;
  }
  .licons{
    text-align: center;
  }
  
  button.licon{
    width:95%;
    height:15vw;
    font-size: 6vw;
    background: none;
    border:2px solid #fff;
    color:white;
  }

  .card-image{
    overflow: hidden;
    position: absolute;
    height:100%;
    width:50%;
    display:flex; 
    justify-content: center;
    align-items: center;
    transition: transform .3s;
    .layers{
      z-index:0;
      width:100%;
      height:100%;
      position:absolute;
      transform-origin: 100% 0;
      transform: rotate(10deg) scale(1.3);
      .arrow{
        position:absolute;
        bottom:25%;
        right:25%;
        width: 30%;
        opacity: 3;
        transition: all .2s;
        border-radius: 100%;
        box-shadow: 0 0 1px 1px #fff inset;
        transform: scale(0);
        transition:transform 1s;
        cursor: pointer;
      }
      .layer{
        width:100%;
        height:100%;
        position:absolute;
        background:rgba(35, 172, 226, 0.4);
        transform-origin: 0 50%;
        transform: scaleX(0);
        transition: transform .5s;
      }
      .l0{
        transition: transform .3s;
      }
      .l1{
        width:95%;
        transition: transform .3s .1s;
      }
      .l2{
        width:90%;
        background:#53BFE8;
        transition: transform .3s .3s;
      }
    }
  }
  .card-image{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.Card:hover{
  .layers{
    .layer{
      transform: scaleX(1);
    }
    .arrow{
      transform: rotate(-10deg) scale(1);
    }
  }
}
  
@media only screen and (max-width:100vh){
  .Card:hover .card-info{
      top:0%;
  }
  .Card{
    height: 20vh;
    width:100vw;
    overflow: hidden;
    display: flex;
    border-radius: 0px;
    background: rgba(255, 255, 255, 0.4);
    box-shadow: 0 0 1px #777 inset;

    .licon{
      margin-top:0;
      width:11%;
    }
    .card-image>.back{
      animation: none;
    }
    .card-info{
      height: 100%;
      width:85%;
      overflow: hidden;
      background: transparent;
      top:0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding:0 2vh;
    }
    .card-image{
      width:30%;
      left:100%;
      transform-origin: 0 50%;
      transform:rotateY(-120deg);
      height: 100%;
      overflow: hidden;
      .layers{
        .arrow{
          box-shadow: none;
          bottom: 35%;
        }
      }
    }
    .card-title{
      line-height: 5vw;
      height:5vw;
      font-size: 5vw;
      text-shadow: 0 0 2px #fff;
    }
    .card-desc{
      font-size: 4vw;
      background: auto;
      background: transparent;
    }
  }
  .Card:hover{
    .card-image{
      .layers{
        .arrow{
          transform: rotate(-10deg) scaleX(2);
        }
      }
    }
  }
}

@keyframes backanim{
  12.5%,42.5%,62.5%,87.5%{
      transform: scale(1.2);
      transform-origin: center;
  }
  25%,50%,75%,100%{
      transform: scale(1);
  }
  25% {
      transform-origin: bottom left;
  }
  50%{
      transform-origin: bottom right;;
  }
  75%{
      transform-origin: top right;
  }
  100%{
      background-position: top left;
  }
}
@keyframes backanim2{
  to{top:100%;}
}