.Accueil{
  .lj-full{
    position: relative;
  }
  .homehead{
    height:60vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
    
  .profilToph{
    box-shadow:0 0 0 .7vh #fff inset;
    border-radius: 100%;
    width:30vh;
    height:30vh;
    background: #888 url(../../assets/img/moi.jpg);
    background-size: cover;
  }
    
  .about{
    top: 0;
    font-weight: 300;
    height: 25vh;
    width: calc(100% - 45vh);
    text-shadow: 0 0 5px #333;
    color:#333;
    color:white;
    position:relative;
  }
  .about .name{
    font-size: 4.3vw;
  }
  
  .about .title{
    font-size: 2.7vw;
    text-align: center;
    font-weight: 100;
  }
  
  .citation{
    font-size: 2.7vw;
    position:absolute;
    right: 1vh;
    text-align: right;
    bottom:0;
    width: 100%;
    color:#eee;
    text-shadow: 0 0 7px rgb(44, 115, 207),0 0 5px rgb(82, 143, 221), 0 0 1px rgb(165, 197, 238);
  }
    
  .plan{
    height:40vh;
    display: flex;
    flex-direction: row;
    position: relative;
    justify-content: space-around;
    align-items: center;
    background: rgba(255,255,255,.5);
    padding: 0 1vh;
  }
  .backplan{
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
  }
  .backplan>div{
    width: 3px;
    height: 150%;
    position: relative;
    overflow: hidden;
    transform-origin: 0 0;
  }
  .backplan>div.monte>div{
    transform-origin: 50% 50%;
    transform: rotateX(180deg);
  }
  .backplan>div>div{
    width:100%;
    height: 100%;
  }
  .backplan>div>div>div{
    position: absolute;
    left: 0;
    top: -5%;
    width:100%;
    height: 5%;
    background: rgb(46, 138, 243);
  }
  .backplan>div.anim>div>div{
    animation: backanim2 1.5s infinite;
  }
  @media only screen and (max-width:800px){
    .homehead{
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height:40vh;
    }
    .profilToph{
        width:25vh;
        height:25vh;
    }
    .about{
        width:100%;
        text-align: center;
        color:#fff;
        height: auto;
    }
    .about .name{
        font-size: 9vw;
    }
  
    .about .title{
        font-size: 5vw;
        text-align: center;
    }
  
    .about .citation{
        display: none
    }
    .plan{
        flex-direction: column;
        padding:0;
        justify-content: space-evenly;
        align-items: center;
        height:60vh;
    }
  }
}
  